.paginations-dot {
    width: 80px;
    height: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.paginations-dot__point {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #ECEBED;
    cursor: pointer;
}

.paginations-dot__point_active {
    background-color: #6979F8;
}